export default class ErmjPathHelper{
    public static readonly basePath = "";
    public static readonly gameAudioPath = ErmjPathHelper.basePath + "sound/";
    public static readonly gamePrefabsPath = ErmjPathHelper.basePath + "prefabs/";
    public static readonly gameTexturePath = ErmjPathHelper.basePath + "texture/";
    public static readonly gameEffectPath = ErmjPathHelper.basePath + "effect/";

    //图集路径
    public atlasPath(atlas)
    {
        return ErmjPathHelper.gameTexturePath + atlas;
    }
}

export class ErmjAudioConst{
    public static audioHumanPath = ErmjPathHelper.gameAudioPath + "human/";
    public static bgm = ErmjPathHelper.gameAudioPath + "bgm";
    public static commonAudio = {
        ButtonClick: ErmjPathHelper.gameAudioPath + "ButtonClick",
        Dice: ErmjPathHelper.gameAudioPath + "Dice",
        DrawCard: ErmjPathHelper.gameAudioPath + "DrawCard",
        Hu: ErmjPathHelper.gameAudioPath + "Hu",
        RewardLose: ErmjPathHelper.gameAudioPath + "Lose",
        OutCard: ErmjPathHelper.gameAudioPath + "OutCard",
        SeatDown: ErmjPathHelper.gameAudioPath + "Seat",
        RewardWin: ErmjPathHelper.gameAudioPath + "Win",
        ContinueGame: ErmjPathHelper.gameAudioPath + "ContinueGame",
        DealCard: ErmjPathHelper.gameAudioPath + "DealCard",
    }
    
    //获取带性别的语音
    public static genderSoundPath(soundName:string, sex:number = 0){
        let genderPath = this.audioHumanPath + "man/"
        if(sex == 1)
            genderPath = this.audioHumanPath + "woman/"
        return genderPath + soundName;
    }
}
